@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
* {
  box-sizing: border-box;
  margin:0;
  padding:0;
  font-family: 'Poppins', sans-serif;
  font-size: medium;
}

/* body {
  overflow:hidden;
} */


.App {
  position:relative;
  max-width: 900px;
  min-height: calc(100vh - 110px);
  margin:60px auto;
  background-color:white;
  color:rgb(16, 15, 15)
}

.container {
  position:relative;
  width:100%;
}

.success-container {
  max-width: 600px;
  height: 300px;
}

.success-box {
  width:100%;
  height: 100%;
  background-color:rgba(0, 0, 0, .5);
  z-index:1000;
  top:0;
  left:0;
}


.title {
  padding-top:30px;
  padding-bottom:50px;
  text-align:center;
}

.form-row {
  position:relative;
  display:grid;
  grid-template-columns: 120px auto;
  width:100%;
  text-align: left;
}





.header {
    position:fixed;
    top:0px;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 60px;
    background-color: rgb(62, 77, 133);
    color:white;
    text-align: center;
    z-index: 10000;
}

.home {
    height:200%;
}

.nav-absolute {
    position:fixed;
    bottom:0;
    height: 50px;
    width: 100%;
    border-top:solid 1px grey;
    background-color: white;
}

.nav-container {
    position: relative;
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: center;
            align-items: center;
    max-width: 900px;
    margin:auto;
}

.nav {
    position:relative;
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: center;
            align-items: center;
    width:100%;
    height: 100%;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    align-items: center;
    color:rgb(27, 27, 27)
}

.tab {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-decoration: none;
    margin:10px;
    color:rgb(61, 60, 60);
}

.tab:hover {
    color:black;
}
